/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header {
	background: $white;
	width: 100%;
	z-index: 10;
	transition: all .3s $cbTrans;
	[class*="fixed-header-"] & {
		position: fixed;
	} 
	.fixed-header-top &,
	.fixed-header-bottom & {
		width: 100%;
	}
	.fixed-header-top & {
		top: 0;
		box-shadow: 0px 5px 0px 0px $black;
	}
	.fixed-header-bottom & {
		bottom: 0;
		box-shadow: 0px -5px 0px 0px $black;
	}
	.fixed-header-left &,
	.fixed-header-right & {
		width: 325px;
		max-width: 100%;
		height: 100%;
		max-height: 100vh;
		top: 0;
		@include breakFirst(medium) {
			width: 30%;
		}
	}
	.fixed-header-left & {
		left: 0;
		box-shadow: 5px 0px 0px 0px $black;
	}
	.fixed-header-right & {
		right: 0;
	    box-shadow: -5px 0px 0px 0px $black;
	}
	&.header-unpinned {
		padding: 0;
	}
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}
}
.site-branding {
    display: block;
    width: 100%;
    a {
		position: relative;
		display: block;
	}
	[class*="fixed-header-"] & {
		a {
			height: 0;
			padding-bottom: 30%;
			width: 100%;
		}
	}
    svg {
		position: absolute; 
		height: 100%; 
		width: 100%; 
		left: 0; 
		top: 0;
    	transition: all .3s $cbTrans;
    }
}
.site-menu-button {
	width: 40px;
	height: 40px;
	position: absolute;
	top: 20px;
	right: 20px;
	@include breakFirst(medium) {
		display: none;
	}
}
.dots {
    display: block;
    height: 40px;
    width: 40px;
    float: right;
    position: absolute;
}
.dot, 
.dot:before, 
.dot:after {
	position: absolute;
	width: 10px;
	height: 10px;
	border-radius: 10px;
	mix-blend-mode: multiply;
	transition: all .3s $cbTrans;
}
.dot {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: $gray;
	&:before,
	&:after {
		content: "";
	}
	&:before {
		right: 15px;
		background-color: $gray;
	}
	&:after {
		left: 15px;
		background-color: $gray;
	}
}
.active-menu {
	overflow: hidden;
	@include breakFirst(medium) {
		overflow: scroll;
	}
	.dot {
		&:before {
			transform: translate(300%, 0);
		}
		&:after {
			transform: translate(-300%, 0);
		}
	}
	.site-header {
		overflow: scroll;
    	max-height: 100vh;	
    	@include breakFirst(medium) {
    		overflow: visible;
    	}
	}
}
.site-navigation {
	display: block;
	width: 100%;
}
#menu-main-menu {
	background: $white;
	li {
		> a {
			padding: 10px;
			display: block;
			background: $grayLight;
			border-bottom: 1px solid $white;
		}
		&:hover {
			> .sub-menu {
				display: block;
			}
		}
	}
	ul {
		ul {
		}
	}
	.sub-menu {
		display: none;
		a {
			background: $gray;
		}
	}
}